import { Component, OnInit } from '@angular/core';
import { EChartsOption } from 'echarts';
import { DatacollectionService } from '../Datacollection-service.service'


@Component({
  selector: 'app-mainDashboardRes',
  templateUrl: './mainDashboardRes.component.html',
  styleUrls: ['./mainDashboardRes.component.scss']
})
export class mainDashboardRes implements OnInit {

  attribute: any = null;
  public metricTableData: any = [];
  public setDataMetricImpact: any = null;
  line_chart: EChartsOption = {
    grid: {
      top: '6',
      right: '0',
      bottom: '35',
      left: '25'
    },
    xAxis: {
      data: ['2014', '2015', '2016', '2017', '2018'],
      axisLine: {
        lineStyle: {
          color: '#eaeaea'
        }
      },
      axisLabel: {
        fontSize: 10,
        color: '#9aa0ac'
      }
    },
    tooltip: {
      show: true,
      showContent: true,
      alwaysShowContent: true,
      triggerOn: 'mousemove',
      // trigger: 'axis'
    },
    legend: {
      data: ['Accuracy', 'Fairness Score'],
      orient: 'horizontal',
      top: 'bottom',
    },
    yAxis: {
      splitLine: {
        lineStyle: {
          color: '#eaeaea'
        }
      },
      axisLine: {
        lineStyle: {
          color: '#eaeaea'
        }
      },
      axisLabel: {
        fontSize: 10,
        color: '#9aa0ac'
      }
    },
    series: [
      {
        name: 'sales',
        type: 'line',
        smooth: true,
        lineStyle: {
          width: 3,
          shadowColor: 'rgba(0,0,0,0.4)',
          shadowBlur: 10,
          shadowOffsetY: 10
        },
        data: [15, 22, 14, 31, 17, 41],
        symbolSize: 10
        // color: ["#FF8D60"]
      },
      {
        name: 'Profit',
        type: 'line',
        smooth: true,
        lineStyle: {
          width: 3,
          shadowColor: 'rgba(0,0,0,0.4)',
          shadowBlur: 10,
          shadowOffsetY: 10
        },
        symbolSize: 10,
        // size: 10,
        data: [8, 12, 28, 10, 10, 12]
        // color: ["#009DA0"]
      }
    ],
    color: ['#3FA7DC', '#F6A025']
  };
  pie_chart2: EChartsOption = {
    legend: {
      top: 'bottom'
    },
    toolbox: {
      show: true,
      feature: {
        mark: { show: true },
        dataView: { show: true, readOnly: false },
        restore: { show: true },
        saveAsImage: { show: true }
      }
    },
    series: [
      {
        name: 'Nightingale Chart',
        type: 'pie',
        radius: '55%',
        center: ['50%', '48%'],
        roseType: 'area',
        itemStyle: {
          borderRadius: 15
        },
        data: [
          { value: 40, name: 'rose 1' },
          { value: 38, name: 'rose 2' },
          { value: 32, name: 'rose 3' },
          { value: 30, name: 'rose 4' },
          { value: 28, name: 'rose 5' },
          { value: 26, name: 'rose 6' },
          { value: 22, name: 'rose 7' },
          { value: 18, name: 'rose 8' }
        ]
      }
    ]
  };

  pie_chart: EChartsOption = {
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b} : {c} ({d}%)'
    },
    legend: {
      data: ['Data 1', 'Data 2', 'Data 3', 'Data 4', 'Data 5'],
      textStyle: {
        color: '#9aa0ac',
        padding: [0, 5, 0, 5]
      }
    },

    series: [
      {
        name: 'Chart Data',
        type: 'pie',
        radius: '55%',
        center: ['50%', '48%'],
        data: [
          {
            value: 335,
            name: 'Data 1'
          },
          {
            value: 310,
            name: 'Data 2'
          },
          {
            value: 234,
            name: 'Data 3'
          },
          {
            value: 135,
            name: 'Data 4'
          },
          {
            value: 548,
            name: 'Data 5'
          }
        ]
      }
    ],
    color: ['#575B7A', '#DE725C', '#DFC126', '#72BE81', '#50A5D8']
  };


  breadscrums = [
    {
      title: 'Bias',
      items: ['Bias'],
      active: 'Summary'
    }
  ];
  constructor(private DatacollectionService: DatacollectionService) { }

  ngOnInit() {


    this.getData()
  }
  getData() {
    this.DatacollectionService.getBiasSummary()
      .subscribe(data => {
        console.log(data, "datasummary")
        if (data) {
          this.setDataMetricImpact = data;
          // this.pie_chart['legend']['data'] = Object.keys(data?.metric_impact_data)

          data?.Fairness_and_Accuracy_Over_Time?.series.map(x => {
            x['lineStyle'] = {
              width: 3,
              shadowColor: 'rgba(0,0,0,0.4)',
              shadowBlur: 10,
              shadowOffsetY: 10
            };
            x['symbolSize'] = 10;
            x['smooth'] = true;
          })
          this.line_chart['series'] = data?.Fairness_and_Accuracy_Over_Time?.series;

          this.line_chart['xAxis']['data'] = data?.Fairness_and_Accuracy_Over_Time?.xAxis;
          this.pie_chart['series'][0]['data'] = data?.metric_impact_data;
          this.pie_chart2['series'][0]['data'] = data?.CorrelationPieChart;
          this.metricTableData = data?.metric_table;
          console.log(JSON.stringify(this.line_chart),"lidsnfjsldfbsjfnds")

        }
      })
    this.DatacollectionService.getAttribute()
      .subscribe(data => {
        if(data){
          this.attribute = data;
        }
      })

  }

}
